import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Post as PostType } from '../../types/post.types';
import { Photo } from '../../types/post.types';
import { Album } from '../../types/album.types';
import { PhotoGrid } from '../PhotoGrid/PhotoGrid';
import { ImageUploader } from '../ImageUploader/ImageUploader';
import { ImageSelector } from '../ImageSelector/ImageSelector';
import { useAuth } from '../../contexts/AuthContext';
import { api } from '../../utils/api';
import styles from './Post.module.css';
import { ServerImage } from '../ServerImage/ServerImage';
import { PhotoViewer } from '../PhotoViewer/PhotoViewer';

interface PostProps {
    post: PostType;
    onDelete?: () => void;
    onUpdate?: (updatedPost: PostType) => void;
}

export const Post: React.FC<PostProps> = ({ post, onDelete, onUpdate }) => {
    const { user } = useAuth();
    const [isEditing, setIsEditing] = useState(false);
    const [editedContent, setEditedContent] = useState(post.content);
    const [editedPhotos, setEditedPhotos] = useState<Photo[]>(post.photos || []);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [liked, setLiked] = useState(false);
    const [likesCount, setLikesCount] = useState(post.likesCount);
    const [isLikeLoading, setIsLikeLoading] = useState(false);
    const [selectedPhoto, setSelectedPhoto] = useState<Photo | null>(null);
    const [selectedPhotoIndex, setSelectedPhotoIndex] = useState<number | null>(null);
    const [renderKey, setRenderKey] = useState(0);

    // Обрабатываем альбомы, если они есть
    const albums = (post as any).albums || [];
    
    // Добавляем дополнительную проверку альбомов
    useEffect(() => {
        if (albums && albums.length > 0) {
            console.log('Альбомы в посте:', albums);
            // Проверяем наличие фотографий в каждом альбоме
            albums.forEach((album: any, index: number) => {
                console.log(`Альбом ${index} (ID: ${album.id}):`, album);
                console.log(`Количество фото: ${album.photosCount}, массив photos:`, album.photos);
                
                if (album.photos && album.photos.length > 0) {
                    console.log('Первое фото альбома:', album.photos[0]);
                } else if (album.photosCount > 0) {
                    console.warn(`Предупреждение: альбом ${album.id} имеет photosCount=${album.photosCount}, но пустой массив photos`);
                    // Если photosCount > 0, но нет фото в массиве, загружаем обложку отдельно
                    loadAlbumCover(album);
                }
            });
        }
    }, [albums]);

    // Функция для загрузки обложки альбома
    const loadAlbumCover = async (album: any) => {
        try {
            console.log(`Загрузка обложки для альбома ${album.id}`);
            const coverPhoto = await api.get(`/albums/${album.id}/cover`);
            
            if (coverPhoto) {
                console.log(`Получена обложка для альбома ${album.id}:`, coverPhoto);
                
                if (!coverPhoto.path) {
                    console.error(`Ошибка: у обложки для альбома ${album.id} отсутствует путь к файлу:`, coverPhoto);
                    return;
                }
                
                // Устанавливаем обложку для альбома
                album.coverPhoto = coverPhoto;
                
                // Добавляем фото в массив photos, если его нет
                if (!album.photos || album.photos.length === 0) {
                    album.photos = [coverPhoto];
                    console.log(`Добавлено фото в массив photos для альбома ${album.id}`);
                }
                
                // Принудительно обновляем состояние компонента
                // Это необходимо, так как React может не заметить изменение вложенного объекта
                setRenderKey(prev => prev + 1);
            } else {
                console.warn(`Не удалось получить обложку для альбома ${album.id}: пустой ответ`);
            }
        } catch (error) {
            console.error(`Ошибка при загрузке обложки для альбома ${album.id}:`, error);
        }
    };

    useEffect(() => {
        // Проверяем, лайкнул ли пользователь этот пост
        const checkLike = async () => {
            try {
                const endpoint = post.wallOwnerId ? `/wall/${post.id}/like` : `/posts/${post.id}/like`;
                console.log('[Post] Проверка лайка:', endpoint);
                const response = await api.get(endpoint);
                console.log('[Post] Результат проверки лайка:', response);
                
                if (response && typeof response.liked === 'boolean') {
                    setLiked(response.liked);
                    if (typeof response.likesCount === 'number') {
                        setLikesCount(response.likesCount);
                    }
                }
            } catch (error) {
                console.error('Ошибка при проверке лайка:', error);
            }
        };

        checkLike();
    }, [post.id, post.wallOwnerId]);

    const handleLike = async () => {
        if (isLikeLoading) return;

        setIsLikeLoading(true);
        try {
            const endpoint = post.wallOwnerId ? `/wall/${post.id}/like` : `/posts/${post.id}/like`;
            console.log('[Post] Отправка запроса на лайк:', endpoint);
            
            const response = await api.post(endpoint, {});
            console.log('[Post] Получен ответ от сервера:', response);
            
            if (!response) {
                throw new Error('Нет ответа от сервера');
            }
            
            if (typeof response.liked === 'boolean' && typeof response.likesCount === 'number') {
                setLiked(response.liked);
                setLikesCount(response.likesCount);
            } else {
                console.error('Некорректный формат ответа:', response);
                throw new Error('Некорректный формат ответа от сервера');
            }
        } catch (error) {
            console.error('Ошибка при обработке лайка:', error);
            setError('Не удалось обработать лайк. Попробуйте позже.');
        } finally {
            setIsLikeLoading(false);
        }
    };

    // Автор может редактировать свой пост
    const canEdit = user && user.id === post.authorId;
    // Автор и владелец стены могут удалять посты
    const canDelete = Boolean(user && (user.id === post.authorId || (post.wallOwnerId && user.id === post.wallOwnerId)));

    const handleDelete = async () => {
        if (!window.confirm('Вы уверены, что хотите удалить этот пост?')) {
            return;
        }

        try {
            setIsSubmitting(true);
            const endpoint = post.wallOwnerId ? `/wall/${post.id}` : `/posts/${post.id}`;
            
            // Сначала удаляем пост
            await api.delete(endpoint);
            
            // Затем удаляем все фотографии поста
            for (const photo of post.photos) {
                await api.delete(`/photos/${photo.id}`);
            }
            
            // Закрываем режим редактирования и вызываем колбэк
            setIsEditing(false);
            onDelete?.();
        } catch (err) {
            console.error('Ошибка при удалении поста:', err);
            setError('Не удалось удалить пост');
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleEdit = async () => {
        if (!user) return;

        setIsSubmitting(true);
        setError(null);

        try {
            const endpoint = post.wallOwnerId ? `/wall/${post.id}` : `/posts/${post.id}`;
            
            // Обновляем пост с новым контентом и только теми фотографиями, которые остались в посте
            const response = await api.put(endpoint, { 
                content: editedContent.trim(),
                photoIds: editedPhotos.map(photo => photo.id)
            });
            
            setIsEditing(false);
            
            // Обновляем пост локально только с оставшимися фотографиями
            const updatedPost = {
                ...post,
                content: editedContent.trim(),
                photos: editedPhotos
            };
            
            // Вызываем колбэк обновления
            onUpdate?.(updatedPost);
            
            // Обновляем локальное состояние
            post.content = editedContent.trim();
            post.photos = editedPhotos;
        } catch (err) {
            console.error('Ошибка при редактировании поста:', err);
            setError('Не удалось отредактировать пост');
        } finally {
            setIsSubmitting(false);
        }
    };

    const handlePhotoDelete = async (photo: Photo) => {
        if (!window.confirm('Вы уверены, что хотите удалить эту фотографию?')) {
            return;
        }

        try {
            // Удаляем фотографию
            await api.delete(`/photos/${photo.id}`);

            // Обновляем локальное состояние
            const updatedPhoto = { ...photo, isDeleted: true };
            
            // Обновляем editedPhotos, сохраняя удаленную фотографию
            setEditedPhotos(prev => prev.map(p => 
                p.id === photo.id ? updatedPhoto : p
            ));
            
            // Обновляем фотографии в посте, сохраняя удаленную фотографию
            post.photos = post.photos.map(p => 
                p.id === photo.id ? updatedPhoto : p
            );
            onUpdate?.(post);
        } catch (err) {
            console.error('Ошибка при удалении фотографии:', err);
            setError('Не удалось удалить фотографию');
        }
    };

    const handleEditPhotoRemove = (photo: Photo) => {
        // В режиме редактирования просто удаляем фотографию из локального состояния
        setEditedPhotos(prev => prev.filter(p => p.id !== photo.id));
        setError(null);
    };

    const handleImageUploaded = (photo: Photo) => {
        setEditedPhotos(prev => [...prev, photo]);
        setError(null);
    };

    const handlePhotoClick = (photo: Photo, index: number) => {
        setSelectedPhoto(photo);
        setSelectedPhotoIndex(index);
    };

    const handlePhotoChange = (photo: Photo) => {
        setSelectedPhoto(photo);
        const index = post.photos.findIndex(p => p.id === photo.id);
        setSelectedPhotoIndex(index);
    };

    return (
        <div className={styles.post}>
            <div className={styles.header}>
                <div className={styles.authorInfo}>
                    <ServerImage
                        path={post.author.avatar?.path}
                        alt={`${post.author.firstName} ${post.author.lastName}`}
                        className={styles.authorAvatar}
                    />
                    <Link to={`/users/${post.author.id}`} className={styles.author}>
                        {post.author.firstName} {post.author.lastName}
                    </Link>
                </div>
                <div className={styles.date}>
                    {new Date(post.createdAt).toLocaleString()}
                </div>
            </div>
            
            {isEditing ? (
                <div className={styles.editContainer}>
                    <textarea
                        className={styles.editTextarea}
                        value={editedContent}
                        onChange={(e) => setEditedContent(e.target.value)}
                        rows={4}
                    />

                    <div className={styles.editPhotos}>
                        <PhotoGrid 
                            photos={editedPhotos}
                            onPhotoDelete={handleEditPhotoRemove}
                            canDelete={true}
                            isEditing={true}
                        />
                        {user && (
                            <ImageSelector 
                                userId={user.id}
                                selectedImages={editedPhotos}
                                onImagesChange={setEditedPhotos}
                            />
                        )}
                    </div>

                    {error && <div className={styles.error}>{error}</div>}

                    <div className={styles.editButtons}>
                        <div className={styles.editButtonsLeft}>
                            <button 
                                className={`${styles.actionButton} ${styles.cancelButton}`}
                                onClick={() => {
                                    setIsEditing(false);
                                    setEditedContent(post.content);
                                    setEditedPhotos(post.photos || []);
                                    setError(null);
                                }}
                                disabled={isSubmitting}
                            >
                                Отмена
                            </button>
                            {canDelete && (
                                <button 
                                    className={`${styles.actionButton} ${styles.deleteButton}`}
                                    onClick={handleDelete}
                                    disabled={isSubmitting}
                                >
                                    Удалить пост
                                </button>
                            )}
                        </div>
                        <div className={styles.editButtonsRight}>
                            <button 
                                className={`${styles.actionButton} ${styles.saveButton}`}
                                onClick={handleEdit}
                                disabled={isSubmitting}
                            >
                                {isSubmitting ? 'Сохранение...' : 'Сохранить'}
                            </button>
                        </div>
                    </div>
                </div>
            ) : (
                <>
                    <div className={styles.content}>{post.content}</div>
                    
                    {/* Отображаем фотографии */}
                    {post.photos && post.photos.length > 0 && (
                        <div className={styles.photoSection}>
                            <h4 className={styles.sectionTitle}>Фотографии</h4>
                            <PhotoGrid 
                                photos={post.photos} 
                                onPhotoDelete={handlePhotoDelete}
                                canDelete={Boolean(canDelete)}
                                isEditing={isEditing}
                                isWallPost={Boolean(post.wallOwnerId)}
                                onPhotoClick={handlePhotoClick}
                            />
                        </div>
                    )}
                    
                    {/* Отображаем альбомы */}
                    {albums.length > 0 && (
                        <div className={styles.albumsSection}>
                            <h4 className={styles.sectionTitle}>Альбомы</h4>
                            {(() => { console.log('Отображение альбомов:', albums); return null; })()}
                            {albums.length === 1 ? (
                                // Специальный режим отображения одного альбома
                                <div className={styles.singleAlbum} key={`album-${albums[0].id}-${renderKey}`}>
                                    <Link 
                                        to={`/albums/${albums[0].id}`} 
                                        className={styles.singleAlbumContainer}
                                    >
                                        <div className={styles.singleAlbumContent}>
                                            <div className={styles.singleAlbumInfo}>
                                                <h3 className={styles.singleAlbumTitle}>{albums[0].title}</h3>
                                                <div className={styles.singleAlbumCount}>
                                                    {albums[0].photosCount || 0} фото
                                                </div>
                                                <div className={styles.singleAlbumButton}>
                                                    Открыть альбом
                                                </div>
                                            </div>
                                            <div className={styles.singleAlbumPreview}>
                                                {(() => { 
                                                    console.log('Альбом в посте:', albums[0]); 
                                                    console.log('Количество фото в альбоме:', albums[0].photosCount);
                                                    console.log('Массив photos:', albums[0].photos);
                                                    return null; 
                                                })()}
                                                
                                                {/* Проверяем, что у альбома есть фотографии по photosCount */}
                                                {albums[0].photosCount > 0 ? (
                                                    <div className={styles.previewGrid}>
                                                        {/* Если у нас есть фотографии в массиве photos, используем их */}
                                                        {albums[0].photos && albums[0].photos.length > 0 ? (
                                                            // Отображаем имеющиеся фотографии
                                                            <>
                                                                {albums[0].photos.slice(0, Math.min(4, albums[0].photos.length)).map((photo: Photo, index: number) => (
                                                                    <div 
                                                                        key={photo.id} 
                                                                        className={`${styles.previewItem} ${index === 0 ? styles.previewItemMain : ''}`}
                                                                    >
                                                                        <ServerImage 
                                                                            path={photo.path}
                                                                            alt={`Фото ${index + 1}`}
                                                                            className={styles.previewImage}
                                                                        />
                                                                    </div>
                                                                ))}
                                                            </>
                                                        ) : albums[0].coverPhoto ? (
                                                            // Если нет фотографий в массиве, но есть обложка, показываем её
                                                            <div className={styles.previewItemMain}>
                                                                <ServerImage 
                                                                    path={albums[0].coverPhoto.path}
                                                                    alt={`Обложка альбома ${albums[0].title}`}
                                                                    className={styles.previewImage}
                                                                />
                                                            </div>
                                                        ) : (
                                                            // Иначе показываем заглушку с информацией, что есть фотографии
                                                            <div className={styles.previewItemMain}>
                                                                <div className={styles.placeholderContent}>
                                                                    <div className={styles.placeholderIcon}>🖼️</div>
                                                                    <div>В альбоме {albums[0].photosCount} фото</div>
                                                                    <div className={styles.placeholderSubtext}>Нажмите, чтобы просмотреть</div>
                                                                </div>
                                                            </div>
                                                        )}
                                                        
                                                        {/* Показываем количество остальных фотографий только если они есть */}
                                                        {albums[0].photosCount > 4 && (
                                                            <div className={styles.morePhotos}>
                                                                +{albums[0].photosCount - 4}
                                                            </div>
                                                        )}
                                                    </div>
                                                ) : (
                                                    // Если в альбоме совсем нет фотографий
                                                    <div className={styles.emptyAlbumPreview}>
                                                        <span>Альбом не содержит фотографий</span>
                                                    </div>
                                                )}
                                            </div>
                                        </div>
                                    </Link>
                                </div>
                            ) : (
                                // Сетка для нескольких альбомов
                                <div className={styles.albumsGrid} key={`albums-grid-${renderKey}`}>
                                    {albums.map((album: Album) => (
                                        <Link 
                                            to={`/albums/${album.id}`} 
                                            key={`album-${album.id}-${renderKey}`}
                                            className={styles.albumCard}
                                        >
                                            <div className={styles.albumCardCover}>
                                                {/* Проверяем наличие обложки или фотографий */}
                                                {album.coverPhoto ? (
                                                    <ServerImage
                                                        path={album.coverPhoto.path}
                                                        alt={album.title}
                                                        className={styles.albumCardImage}
                                                    />
                                                ) : album.photos && album.photos.length > 0 ? (
                                                    <ServerImage
                                                        path={album.photos[0].path}
                                                        alt={album.title}
                                                        className={styles.albumCardImage}
                                                    />
                                                ) : album.photosCount > 0 ? (
                                                    <div className={styles.emptyAlbumCover}>
                                                        <span>Есть {album.photosCount} фото</span>
                                                    </div>
                                                ) : (
                                                    <div className={styles.emptyAlbumCover}>
                                                        <span>Нет фото</span>
                                                    </div>
                                                )}
                                                <div className={styles.albumCardOverlay}>
                                                    <div className={styles.albumCardTitle}>{album.title}</div>
                                                    <div className={styles.albumCardCount}>
                                                        {album.photosCount || 0} фото
                                                    </div>
                                                </div>
                                            </div>
                                        </Link>
                                    ))}
                                </div>
                            )}
                        </div>
                    )}

                    {selectedPhoto && (
                        <PhotoViewer
                            photo={selectedPhoto}
                            onClose={() => {
                                setSelectedPhoto(null);
                                setSelectedPhotoIndex(null);
                            }}
                            onDelete={canDelete ? () => handlePhotoDelete(selectedPhoto) : undefined}
                            canDelete={canDelete}
                            isWallPost={Boolean(post.wallOwnerId)}
                            allPhotos={post.photos}
                            currentIndex={selectedPhotoIndex || 0}
                            onPhotoChange={handlePhotoChange}
                        />
                    )}

                    <div className={styles.footer}>
                        <button 
                            className={`${styles.actionButton} ${styles.likeButton} ${liked ? styles.liked : ''}`}
                            onClick={handleLike}
                            disabled={isLikeLoading}
                        >
                            {isLikeLoading ? '...' : liked ? 'Нравится' : 'Нравится'} • {likesCount}
                        </button>
                        
                        <div className={styles.actions}>
                            <button className={styles.actionButton}>
                                💬 {post.commentsCount || 0}
                            </button>
                            <button className={styles.actionButton}>
                                🔄 {post.sharesCount || 0}
                            </button>
                        </div>

                        <div className={styles.modifyButtons}>
                            {canEdit && !isEditing && (
                                <button
                                    className={`${styles.actionButton} ${styles.editButton}`}
                                    onClick={() => setIsEditing(true)}
                                >
                                    Редактировать
                                </button>
                            )}
                        </div>
                    </div>
                </>
            )}

            {error && <div className={styles.error}>{error}</div>}
        </div>
    );
};