import React, { useState, useRef, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import styles from './UploadAudio.module.css';

// API URL из переменных окружения
const API_URL = process.env.REACT_APP_API_URL || 'https://blue-bikes-happen.loca.lt/api';

interface UploadAudioProps {
    onTrackUploaded?: (newTrack: any) => void;
}

interface UploadStatus {
    file: File;
    progress: number;
    status: 'pending' | 'uploading' | 'completed' | 'error';
    message?: string;
}

const UploadAudio: React.FC<UploadAudioProps> = ({ onTrackUploaded }) => {
    const { user } = useAuth();
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isUploading, setIsUploading] = useState(false);
    const [uploadStatuses, setUploadStatuses] = useState<UploadStatus[]>([]);
    const [audioFiles, setAudioFiles] = useState<File[]>([]);
    const [coverFileName, setCoverFileName] = useState('');
    const formRef = useRef<HTMLFormElement>(null);
    const coverPreviewRef = useRef<HTMLImageElement>(null);
    const modalRef = useRef<HTMLDivElement>(null);
    const [overallProgress, setOverallProgress] = useState(0);

    // Обработчик нажатия клавиши Escape для закрытия модального окна
    useEffect(() => {
        const handleEscKey = (event: KeyboardEvent) => {
            if (event.key === 'Escape' && isModalOpen) {
                closeModal();
            }
        };

        document.addEventListener('keydown', handleEscKey);
        return () => {
            document.removeEventListener('keydown', handleEscKey);
        };
    }, [isModalOpen]);

    // Предотвращение скролла при открытом модальном окне
    useEffect(() => {
        if (isModalOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = '';
        }

        return () => {
            document.body.style.overflow = '';
        };
    }, [isModalOpen]);

    // Обработчик клика вне модального окна
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (modalRef.current && 
                event.target instanceof Element && 
                !modalRef.current.contains(event.target) && 
                event.target.classList.contains(styles.modalOverlay)) {
                closeModal();
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
        };
    }, []);

    const openModal = () => {
        setIsModalOpen(true);
    };

    const closeModal = () => {
        if (isUploading) return; // Предотвращаем закрытие во время загрузки
        setIsModalOpen(false);
        resetForm();
    };

    const resetForm = () => {
        if (formRef.current) {
            formRef.current.reset();
        }
        setAudioFiles([]);
        setCoverFileName('');
        setUploadStatuses([]);
        setOverallProgress(0);
        
        if (coverPreviewRef.current) {
            coverPreviewRef.current.style.display = 'none';
        }
    };

    const handleAudioFilesChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const files = e.target.files;
        if (files && files.length > 0) {
            const fileArray = Array.from(files);
            setAudioFiles(fileArray);
            
            // Создаем статусы загрузки для каждого файла
            const newUploadStatuses = fileArray.map(file => ({
                file,
                progress: 0,
                status: 'pending' as const
            }));
            
            setUploadStatuses(newUploadStatuses);
        } else {
            setAudioFiles([]);
            setUploadStatuses([]);
        }
    };

    const handleCoverFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (file) {
            setCoverFileName(file.name);
            
            // Показываем предпросмотр обложки
            if (coverPreviewRef.current) {
                const reader = new FileReader();
                reader.onload = (event) => {
                    if (coverPreviewRef.current && event.target?.result) {
                        coverPreviewRef.current.src = event.target.result as string;
                        coverPreviewRef.current.style.display = 'block';
                    }
                };
                reader.readAsDataURL(file);
            }
        } else {
            setCoverFileName('');
            if (coverPreviewRef.current) {
                coverPreviewRef.current.style.display = 'none';
            }
        }
    };

    const updateFileStatus = (file: File, updates: Partial<UploadStatus>) => {
        setUploadStatuses(prevStatuses => 
            prevStatuses.map(status => 
                status.file === file ? { ...status, ...updates } : status
            )
        );
    };

    const uploadFile = async (file: File, coverImageFile: File | null): Promise<any> => {
        return new Promise((resolve, reject) => {
            // Обновляем статус файла на 'uploading'
            updateFileStatus(file, { status: 'uploading' });
            
            const formData = new FormData();
            formData.append('audioFile', file);
            
            if (coverImageFile) {
                formData.append('coverImage', coverImageFile);
            }
            
            // Используем метаданные из файла, оставляя поля пустыми 
            // - сервер извлечет их из метаданных
            formData.append('extractMetadata', 'true');
            
            const xhr = new XMLHttpRequest();
            xhr.open('POST', `${API_URL}/music/upload`, true);
            xhr.withCredentials = true;
            xhr.setRequestHeader('Accept', 'application/json');
            
            xhr.upload.onprogress = (e) => {
                if (e.lengthComputable) {
                    const progress = Math.round((e.loaded / e.total) * 100);
                    updateFileStatus(file, { progress });
                    
                    // Обновляем общий прогресс
                    updateOverallProgress();
                }
            };
            
            xhr.onload = () => {
                if (xhr.status === 201) {
                    try {
                        const response = JSON.parse(xhr.responseText);
                        updateFileStatus(file, { 
                            status: 'completed',
                            progress: 100,
                            message: 'Загружен успешно'
                        });
                        resolve(response);
                    } catch (parseError) {
                        updateFileStatus(file, { 
                            status: 'error', 
                            message: 'Ошибка при обработке ответа сервера'
                        });
                        reject(parseError);
                    }
                } else {
                    updateFileStatus(file, { 
                        status: 'error', 
                        message: `Ошибка ${xhr.status}: ${xhr.statusText}`
                    });
                    reject(new Error(`${xhr.status} ${xhr.statusText}`));
                }
                
                // Обновляем общий прогресс
                updateOverallProgress();
            };
            
            xhr.onerror = () => {
                updateFileStatus(file, { 
                    status: 'error', 
                    message: 'Ошибка соединения с сервером'
                });
                reject(new Error('Ошибка соединения с сервером'));
                updateOverallProgress();
            };
            
            xhr.send(formData);
        });
    };

    const updateOverallProgress = () => {
        // Рассчитываем средний прогресс всех файлов
        if (uploadStatuses.length === 0) return;
        
        const totalProgress = uploadStatuses.reduce((acc, status) => acc + status.progress, 0);
        const average = Math.round(totalProgress / uploadStatuses.length);
        setOverallProgress(average);
    };

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        
        if (!user) {
            alert('Пожалуйста, войдите в систему для загрузки треков');
            return;
        }
        
        if (audioFiles.length === 0) {
            alert('Пожалуйста, выберите хотя бы один аудиофайл');
            return;
        }
        
        // Проверяем, не слишком ли много файлов
        if (audioFiles.length > 100) {
            alert(`Вы выбрали ${audioFiles.length} файлов. Максимально допустимое количество: 100 файлов за раз.`);
            return;
        }

        setIsUploading(true);
        
        // Получаем файл обложки (если есть)
        const coverImage = (e.currentTarget.elements.namedItem('coverImage') as HTMLInputElement)?.files?.[0] || null;
        
        // Проверяем, сколько файлов будем загружать
        if (audioFiles.length === 1) {
            // Для одного файла используем обычный эндпоинт
            try {
                const response = await uploadFile(audioFiles[0], coverImage);
                
                if (onTrackUploaded) {
                    const validTrack = {
                        id: response.id || 0,
                        title: response.title || 'Неизвестный трек',
                        artist: response.artist || 'Неизвестный исполнитель',
                        duration: response.duration || 0,
                        coverUrl: response.coverUrl || '/api/music/cover/default.png',
                        audioUrl: `${API_URL}/music/file/${response.filename}`,
                        playCount: response.playCount || 0
                    };
                    onTrackUploaded(validTrack);
                }
                
                setTimeout(() => {
                    closeModal();
                    resetForm();
                }, 1500);
            } catch (error) {
                console.error('[UploadAudio] Ошибка при загрузке трека:', error);
            } finally {
                setIsUploading(false);
            }
        } else {
            // Для нескольких файлов используем множественную загрузку
            try {
                // Проверяем, не слишком ли большой размер файлов
                const totalSize = audioFiles.reduce((sum, file) => sum + file.size, 0);
                const maxSize = 1000 * 1024 * 1024; // 1 ГБ
                
                if (totalSize > maxSize) {
                    alert(`Общий размер выбранных файлов (${Math.round(totalSize / (1024 * 1024))} МБ) превышает максимально допустимый (${Math.round(maxSize / (1024 * 1024))} МБ)`);
                    setIsUploading(false);
                    return;
                }
                
                // Создаем FormData с несколькими файлами
                const formData = new FormData();
                
                // Добавляем все аудиофайлы
                audioFiles.forEach(file => {
                    formData.append('audioFile', file);
                });
                
                // Добавляем обложку, если есть
                if (coverImage) {
                    formData.append('coverImage', coverImage);
                }
                
                // Указываем, что нужно извлечь метаданные
                formData.append('extractMetadata', 'true');
                
                // Создаем запрос для отправки нескольких файлов
                const xhr = new XMLHttpRequest();
                xhr.open('POST', `${API_URL}/music/upload/multiple`, true);
                xhr.withCredentials = true;
                xhr.setRequestHeader('Accept', 'application/json');
                
                // Обработка прогресса загрузки
                xhr.upload.onprogress = (e) => {
                    if (e.lengthComputable) {
                        const totalProgress = Math.round((e.loaded / e.total) * 100);
                        setOverallProgress(totalProgress);
                        
                        // Распределяем прогресс между файлами примерно равномерно
                        const averageProgress = totalProgress;
                        audioFiles.forEach(file => {
                            updateFileStatus(file, { 
                                status: 'uploading', 
                                progress: averageProgress 
                            });
                        });
                    }
                };
                
                // Обработка завершения запроса
                xhr.onload = () => {
                    if (xhr.status === 201) {
                        try {
                            const response = JSON.parse(xhr.responseText);
                            console.log('[UploadAudio] Ответ от сервера:', response);
                            
                            // Показываем общую статистику
                            if (response.totalUploaded > 0 || response.totalFailed > 0) {
                                console.log(`[UploadAudio] Загружено: ${response.totalUploaded}, Ошибок: ${response.totalFailed}`);
                            }
                            
                            // Обновляем статус для каждого файла
                            if (response.results && Array.isArray(response.results)) {
                                // Создаем мапу для быстрого поиска результатов по имени файла
                                const resultsByName = new Map();
                                response.results.forEach((result: any) => {
                                    resultsByName.set(result.originalName, result);
                                });
                                
                                // Создаем мапу для ошибок
                                const errorsByName = new Map();
                                if (response.errors && Array.isArray(response.errors)) {
                                    response.errors.forEach((error: any) => {
                                        errorsByName.set(error.originalName, error);
                                    });
                                }
                                
                                // Обновляем статусы файлов
                                audioFiles.forEach(file => {
                                    const fileResult = resultsByName.get(file.name);
                                    const fileError = errorsByName.get(file.name);
                                    
                                    if (fileResult && fileResult.success) {
                                        updateFileStatus(file, { 
                                            status: 'completed', 
                                            progress: 100,
                                            message: 'Загружен успешно'
                                        });
                                        
                                        // Вызываем колбэк onTrackUploaded для каждого успешно загруженного трека
                                        if (onTrackUploaded && fileResult.track) {
                                            const track = fileResult.track;
                                            const validTrack = {
                                                id: track.id || 0,
                                                title: track.title || 'Неизвестный трек',
                                                artist: track.artist || 'Неизвестный исполнитель',
                                                duration: track.duration || 0,
                                                coverUrl: track.coverUrl || '/api/music/cover/default.png',
                                                audioUrl: `${API_URL}/music/file/${track.filename}`,
                                                playCount: track.playCount || 0
                                            };
                                            onTrackUploaded(validTrack);
                                        }
                                    } else if (fileError) {
                                        updateFileStatus(file, { 
                                            status: 'error', 
                                            progress: 100,
                                            message: fileError.error || 'Ошибка при загрузке'
                                        });
                                    } else {
                                        // Если не нашли ни результата, ни ошибки
                                        updateFileStatus(file, { 
                                            status: 'error', 
                                            progress: 100,
                                            message: 'Нет информации о результате загрузки'
                                        });
                                    }
                                });
                            } else {
                                // Если нет массива результатов
                                audioFiles.forEach(file => {
                                    updateFileStatus(file, { 
                                        status: 'error', 
                                        message: 'Ошибка при загрузке'
                                    });
                                });
                            }
                            
                            // Если хотя бы один файл успешно загружен, закрываем модальное окно
                            const hasSuccess = response.totalUploaded > 0;
                            if (hasSuccess) {
                                setTimeout(() => {
                                    closeModal();
                                    resetForm();
                                }, 3000); // Увеличиваем задержку до 3 секунд
                            }
                        } catch (parseError) {
                            console.error('[UploadAudio] Ошибка при обработке ответа:', parseError);
                            // Помечаем все файлы как ошибочные
                            audioFiles.forEach(file => {
                                updateFileStatus(file, { 
                                    status: 'error', 
                                    message: 'Ошибка при обработке ответа'
                                });
                            });
                        }
                    } else {
                        console.error('[UploadAudio] Ошибка HTTP:', xhr.status, xhr.statusText);
                        // Помечаем все файлы как ошибочные
                        audioFiles.forEach(file => {
                            updateFileStatus(file, { 
                                status: 'error', 
                                message: `Ошибка ${xhr.status}: ${xhr.statusText}`
                            });
                        });
                    }
                    
                    setIsUploading(false);
                };
                
                xhr.onerror = () => {
                    console.error('[UploadAudio] Сетевая ошибка при загрузке');
                    // Помечаем все файлы как ошибочные
                    audioFiles.forEach(file => {
                        updateFileStatus(file, { 
                            status: 'error', 
                            message: 'Ошибка соединения с сервером'
                        });
                    });
                    
                    setIsUploading(false);
                };
                
                // Показываем дополнительное сообщение при большом количестве файлов
                if (audioFiles.length > 20) {
                    updateFileStatus(audioFiles[0], {
                        status: 'pending',
                        message: `Загрузка ${audioFiles.length} файлов может занять некоторое время. Пожалуйста, ожидайте.`
                    });
                }
                
                // Отправляем запрос
                xhr.send(formData);
                
            } catch (error) {
                console.error('[UploadAudio] Общая ошибка при множественной загрузке:', error);
                setIsUploading(false);
            }
        }
    };

    return (
        <>
            <button 
                className={styles.uploadButton}
                onClick={openModal}
                aria-label="Загрузить аудио"
                title="Загрузить новые треки"
            >
                <svg 
                    className={styles.uploadButtonIcon} 
                    viewBox="0 0 24 24" 
                    fill="currentColor"
                >
                    <path d="M9 16h6v-6h4l-7-7-7 7h4v6zm-4 2h14v2H5v-2z"/>
                </svg>
            </button>

            {isModalOpen && (
                <div className={styles.modalOverlay}>
                    <div className={styles.modalContent} ref={modalRef}>
                        <div className={styles.modalTitle}>
                            <span>Загрузка музыки</span>
                            <button 
                                className={styles.closeButton} 
                                onClick={closeModal}
                                disabled={isUploading}
                                aria-label="Закрыть"
                            >
                                ×
                            </button>
                        </div>

                        <form 
                            ref={formRef}
                            className={styles.uploadForm} 
                            onSubmit={handleSubmit}
                            encType="multipart/form-data"
                        >
                            <div className={styles.formGroup}>
                                <label htmlFor="audioFile" className={styles.label}>
                                    Аудиофайлы *
                                </label>
                                <div className={styles.fileInputWrap}>
                                    <label htmlFor="audioFile" className={styles.fileInputLabel}>
                                        <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                            <path d="M2 12.5C2 9.46 4.46 7 7.5 7H18c2.21 0 4 1.79 4 4s-1.79 4-4 4H9.5C8.12 15 7 13.88 7 12.5S8.12 10 9.5 10H17v2H9.41c-0.55 0-0.55 1 0 1H18c1.1 0 2-0.9 2-2s-0.9-2-2-2H7.5C5.57 9 4 10.57 4 12.5S5.57 16 7.5 16H17v2H7.5C4.46 18 2 15.54 2 12.5z"/>
                                        </svg>
                                        Выбрать аудиофайлы
                                    </label>
                                    <input 
                                        type="file" 
                                        id="audioFile" 
                                        name="audioFile" 
                                        className={styles.fileInput} 
                                        accept="audio/*"
                                        onChange={handleAudioFilesChange}
                                        multiple
                                        required
                                        disabled={isUploading}
                                    />
                                    {audioFiles.length > 0 && (
                                        <div className={styles.fileCount}>
                                            Выбрано файлов: {audioFiles.length}
                                        </div>
                                    )}
                                </div>
                            </div>

                            <div className={styles.formGroup}>
                                <label htmlFor="coverImage" className={styles.label}>
                                    Обложка (опционально)
                                </label>
                                <div className={styles.fileInputWrap}>
                                    <label htmlFor="coverImage" className={styles.fileInputLabel}>
                                        <svg width="20" height="20" viewBox="0 0 24 24" fill="currentColor">
                                            <path d="M21 19V5c0-1.1-.9-2-2-2H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2zM8.5 13.5l2.5 3.01L14.5 12l4.5 6H5l3.5-4.5z"/>
                                        </svg>
                                        Выбрать изображение
                                    </label>
                                    <input 
                                        type="file" 
                                        id="coverImage" 
                                        name="coverImage" 
                                        className={styles.fileInput} 
                                        accept="image/*"
                                        onChange={handleCoverFileChange}
                                        disabled={isUploading}
                                    />
                                    {coverFileName && (
                                        <div className={styles.fileName}>
                                            {coverFileName}
                                        </div>
                                    )}
                                </div>
                                <img 
                                    ref={coverPreviewRef}
                                    src="#" 
                                    alt="Предпросмотр" 
                                    className={styles.coverPreview} 
                                    id="coverPreview"
                                />
                            </div>

                            <div className={styles.infoMessage}>
                                <p>Информация о треках (исполнитель, название) будет извлечена автоматически из метаданных файлов.</p>
                            </div>

                            {uploadStatuses.length > 0 && (
                                <div className={styles.uploadStatusList}>
                                    <h4 className={styles.uploadStatusTitle}>Статус загрузки:</h4>
                                    <div className={styles.overallProgress}>
                                        <div className={styles.overallProgressLabel}>
                                            Общий прогресс: {overallProgress}%
                                        </div>
                                        <div className={styles.progressBarContainer}>
                                            <div 
                                                className={styles.progressBarFill} 
                                                style={{ width: `${overallProgress}%` }}
                                            >
                                            </div>
                                        </div>
                                    </div>
                                    <div className={styles.fileStatusList}>
                                        {uploadStatuses.map((status, index) => (
                                            <div 
                                                key={index} 
                                                className={`${styles.fileStatus} ${styles[status.status]}`}
                                            >
                                                <div className={styles.fileStatusName}>
                                                    {status.file.name}
                                                </div>
                                                <div className={styles.fileStatusProgress}>
                                                    <div className={styles.progressBarContainer}>
                                                        <div 
                                                            className={styles.progressBarFill} 
                                                            style={{ width: `${status.progress}%` }}
                                                        >
                                                        </div>
                                                    </div>
                                                    <div className={styles.progressPercent}>
                                                        {status.progress}%
                                                    </div>
                                                </div>
                                                {status.message && (
                                                    <div className={styles.fileStatusMessage}>
                                                        {status.message}
                                                    </div>
                                                )}
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            )}

                            <div className={styles.formActions}>
                                <button 
                                    type="button" 
                                    className={styles.cancelButton}
                                    onClick={closeModal}
                                    disabled={isUploading}
                                >
                                    Отмена
                                </button>
                                <button 
                                    type="submit" 
                                    className={styles.submitButton}
                                    disabled={isUploading || audioFiles.length === 0}
                                >
                                    {isUploading ? 'Загрузка...' : 'Загрузить'}
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}
        </>
    );
};

export default UploadAudio; 